import React, { useRef } from 'react';
import { Circle } from 'react-konva';
import useImage from "use-image"
interface Props {
    shapeProps: any
    onSelect: any
    onChange: any
}

const UCircle: React.FC<Props> = ({ shapeProps, onSelect, onChange }) => {
    const shapeRef = useRef(null)

    const { patternImageUrl, ...restProps } = shapeProps
    const [image] = useImage(patternImageUrl || null)
    const circleWidth = shapeRef.current?.getClientRect().width;
    const circleHeight = shapeRef.current?.getClientRect().height;
    const imageWidth = image ? image.width : 10;
    const imageHeight = image ? image.height : 10;

    // testing
    const Xpoint = (imageWidth / 2)
    const Ypoint = (imageHeight / 2)



    // const patternScale = restProps.patternScale || Math.max(
    //     circleWidth / imageWidth,
    //     circleHeight / imageHeight
    // );

    console.log({ Xpoint, Ypoint, circleHeight, circleWidth, imageHeight, imageWidth })

    return (
        <React.Fragment>
            <Circle
                ref={shapeRef}
                onClick={onSelect}
                onTap={onSelect}
                draggable
                strokeScaleEnabled={false}
                onTransformEnd={(e) => {
                    onChange({
                        ...shapeProps,
                        // scaleX: e.target.attrs.scaleX,
                        // scaleY: e.target.attrs.scaleY,
                    });
                }}
                onDragEnd={(e) => {
                    onChange({
                        ...shapeProps,
                        x: e.target.x(),
                        y: e.target.y(),
                    });
                }}
                {...restProps}
                fillPatternImage={image}
                fillPatternOffset={{ x: Xpoint, y: Ypoint }}
                fillPatternScaleY={1.4 * (circleWidth / circleHeight)}
                fillPatternScaleX={1.4 * (circleHeight / circleWidth)}
                fillPatternRepeat="no-repeat"
            />
        </React.Fragment>
    );
};
export default UCircle
